import React, {Component} from 'react';
import store from '../store';
import {connect} from 'react-redux';
import UserBoards from '../components/UserBoards';
import {getBoardNotes, getBoard} from '../actions/board';
import {getAllNotes} from '../actions/note';

class UserBoardsContainer extends Component {

  componentDidMount() {
    this.props.onBoardEnter();
  }

  render() {
    console.log(this.props);
    return (
      // <Board {...this.props} />
      <div style={{
        width   : 1000,
        height  : 1000,
        position: 'relative'}}>
        {this.props.notes.map((note) => {
          const { left, top } = note;

          return (
            <NoteWrapper
              key={note.id}
              id={note.id}
              left={left}
              top={top}
              note={note}
            >

            </NoteWrapper>
          );
        })}
      </div>
    );
  }
}


const mapStateToProps = (state) => ({notes: state.noteReducer.all});

const mapDispatchToProps = (dispatch, ownProps) => (
  {
    onBoardEnter: function() {
      console.log(ownProps);
      const boardId = ownProps.board.id;
      dispatch(getBoard(boardId));
      dispatch(getAllNotes({boardId}));
    }
  });

export default connect(mapStateToProps, mapDispatchToProps)(UserBoardsContainer);



